import { useEffect, useState } from "react";
import useSharing from "../hooks/useSharing"
import Journal from "./Journal";

function SharedWithMe ({shareId, name, display}) {
  const {getShareById} = useSharing ();
  const [journal, setJournal] = useState ([]);
  useEffect (() => {
    if (!shareId) return;
    (async () => {
      try {
        setJournal (await getShareById (shareId));
      } catch (e) {
        alert (e);
      }
    }) ();
  }, [shareId]);
  return (
    <>
      <main style={{display}}>
        <h2>Joural Of {name}</h2>
        <Journal display={display} entries={journal} />
      </main>
    </>
  )
}

export default SharedWithMe 